import React, { useEffect, useState } from "react";
import skullcandy from "../../../assets/skullcandy_pattern.png"
import { Parallax } from 'react-scroll-parallax';
import './css/ParallaxSkull.css'


export default function ParallaxSkull() {
    const [offsetY, setOffsetY] = useState(0);
    const handleScroll = () => setOffsetY(window.pageYOffset);
    const speed = 0.7;

    useEffect(() => {
        window.addEventListener("scroll", handleScroll);
        return () => {
            window.removeEventListener("scroll", handleScroll);
        };
    }, []);

    return (
        <div className='setUp'>
            <div className='outer_skull_container'>
                <div className='same_line_skull'>
                    <span className='academic'>Academic Project</span>
                    <span className='year'>Year: 2019 - 2020</span>
                </div>
                <div className='skull_container'>
                    <div className='skullcandy_pattern'>
                        <img src={skullcandy} style={{width: '50vw'}}></img>
                    </div>
                    <div className='text_container_skull'>
                        <div className='text_1'>
                            <div className='upper_text_1' style={{ transform: `translateX(${offsetY * speed}px)` }}> Complete Rebrand for</div>
                            <div className='lower_text_1' style={{ transform: `translateX(${offsetY * speed}px)` }}>Complete Rebrand for</div>
                        </div>
                        <div className='text_2'>
                            <div className='upper_text_2' style={{ transform: `translateX(-${offsetY * speed}px)` }}>Skullcandy studios</div>
                            <div className='lower_text_2' style={{ transform: `translateX(-${offsetY * speed}px)` }}>Skullcandy studios</div>
                        </div>
                    </div>
                </div>
            </div>

        </div>
    )
}