import React from 'react'
import gallery_1 from '../../../../assets/Graphic_Design/book_1.png'
import gallery_2 from '../../../../assets/Graphic_Design/book_2.png'
import gallery_3 from '../../../../assets/Graphic_Design/book_3.png'
import gallery_4 from '../../../../assets/Graphic_Design/book_4.png'

import '../css/Gallery.css'

export default function Gallery2(){

    const styles = {
        width:'34vw',
        padding:'25px 25px 25px 25px'
    }

        return(
            <div className='setUp' style={{paddingTop:'30vh', paddingBottom:'10vh'}}>
                <div style={{margin:'0 auto', width:'50%', textAlign:'center', color:'white', fontSize:'2vw',paddingTop:'20vh'}}>Comic Strip</div>
                <div style ={{textAlign:'center'}}>
                    <p style={{lineHeight:'5px'}}>The Lockdown affected all of us, work from home became the new normal , here is a short comic strip on </p>
                    <p style={{lineHeight:'5px'}}>the changes in my lifestyle during lockdown .</p><br/>
                    <p style={{fontWeight:'600'}}>YEAR :  2019</p>
                    <p style={{lineHeight:'0px'}}>PERSONAL PROJECT</p>
                </div>
                <div className='center' style={{paddingTop:'10vh'}}>
                    <div>
                        <img src={gallery_1} style={styles}/>
                        <img src={gallery_2} style={styles}/><br/>
                        <img src={gallery_3} style={styles}/>
                        <img src={gallery_4} style={styles}/>
                    </div>
                </div>
            </div>
        )
}