import React from "react";
import HoverVideoPlayer from 'react-hover-video-player';
import './css/VideoPlayer.css'
import video1 from '../../../assets/video/video1.mp4'
import video2 from '../../../assets/video/video2.mov'
import video3 from '../../../assets/video/video3.mov'
import video4 from '../../../assets/video/video4.mp4'


export default function VideoPlayer() {
    return (
        <div className='setUp'>
            <div className='all_content_video_player'>
                <div style={{ paddingTop: '5vh', paddingBottom: '5vh' }}><p style={{fontSize:'2rem'}}>Channel Identifier</p>
                    <p>A Frame by Frame animation done for the channel  NICKELODEON </p></div>

                <HoverVideoPlayer
                    className='video_1'
                    videoSrc={video1}
                    muted={false}

                />
                <div style={{ paddingTop: '5vh', paddingBottom: '5vh' }}><p style={{fontSize:'2rem'}}>Red Dot Animation</p>
                    <p>A group project done by the CG students of our college,</p>
                    <p>our brief was visualising a  RED DOT  through animation media.</p></div>

                <HoverVideoPlayer
                    className='video_2'
                    videoSrc={video2}
                />
                <div style={{ paddingTop: '5vh', paddingBottom: '5vh' }}><p style={{fontSize:'2rem'}}>Animated Movie Scene</p>
                    <p>A animated scene done from the movie  DR . STRANGE . </p>
                    <p> The animation is done with a mix of CG/VFX and traditional rendering of the character</p></div>

                <HoverVideoPlayer
                    className='video_3'
                    videoSrc={video3}
                    muted={false}
                    
                />
                <div style={{ paddingTop: '5vh', paddingBottom: '5vh' }}><p style={{fontSize:'2rem'}}>Communication Poster Animation</p>
                </div>


                <HoverVideoPlayer
                    className='video_4'
                    videoSrc={video4}
                />
            </div>

        </div>
    )
}