import React, { useEffect, useState, useRef } from "react";
import hoverEffect from "hover-effect";
import overlay from "../../../../assets/displacement_image.jpg"
import img1 from "../../../../assets/Graphic_Design/kissan.png"
import img2 from "../../../../assets/Branding/graphic_design_image.jpg"
import img3 from "../../../../assets/test_img.jpg"
import '../css/Kisanjam.css'
 


export default function Kisanjam() {

    const container = useRef(null);
    useEffect(() => {

        new hoverEffect({
            parent: container.current,
            intensity: .1,
            image1: img1,
            image2: img2,
            displacementImage: overlay,
            imagesRatio: 0.5625,
        })
    },[container])

    const styles ={
        
    }

    return (

            <div className='setUp' style={{paddingBottom:'60vh'}}>
                <div className='pos_relative'>
                    <div className='kisan_title' style={{height:'50vh'}}>
                        <p className='big_title'>Graphic Design</p>
                        <p className='smallBig_title'>Kissan Jam Package Redesign</p>
                        <p style={{lineHeight:'5px',fontSize:'1.4rem'}}>The famous Indian jam which we all grew up eating,  I decided to take up the challenge to </p>
                        <p style={{lineHeight:'5px',fontSize:'1.4rem'}}>give the package a whole new look with an illustrative style, while still resembling the </p>
                        <p style={{lineHeight:'5px',fontSize:'1.4rem'}}>fun attitude of the brand and the flavours it has to offer.</p><br/>
                        <p style={{fontWeight:600,fontSize:'1.4rem',lineHeight:'20px'}}>YEAR :  2018</p>
                        <p style={{lineHeight:'0px'}}>PERSONAL PROJECT</p>
                    </div>
                    <div
                        className='imgContainer'
                        id="imgContainer"
                        style={{width:'80vw',
                                height:'95vh',
                            paddingTop:'30vh'}}
                        ref={container}/>
                </div>

            </div>
    )
}