import React from 'react'
import gallery_1 from '../../../../assets/Graphic_Design/gallery_1.jpg'
import gallery_2 from '../../../../assets/Graphic_Design/gallery_2.png'
import gallery_3 from '../../../../assets/Graphic_Design/gallery_3.png'
import '../css/Gallery.css'


export default function Gallery() {

    const styles = {
        width: '25vw',
        paddingLeft:'25px',
        paddingRight:'25px',
        left: '20vw'
    }

    return (
        <div className='setUp' style={{ paddingTop: '60vh' }}>
            <div style={{ margin: '0 auto', width: '50%', textAlign: 'center', color: 'white', fontSize: '2vw', paddingTop: '10vh' }}>Posters</div>
            <div className='center'>
                <div className='float_left'>
                    <div class="container">
                        <div className="content">
                            <div class="content-overlay"></div>
                            <img className="content-image" style={styles} src={gallery_1} />
                            <div className="content-details fadeIn-bottom" style={styles}>
                                <p className="upper-text">YEAR: 2021</p>
                                <p className="upper-text">Personal Project</p>
                                <h3 className="content-title">Communication Design Poster</h3>
                                <p className="content-text" style={{ lineHeight: '3px' }}>A poster on my personal favourite artist</p>
                                <p className="content-text" style={{ lineHeight: '3px' }}>Trying to show his style and colour</p>
                                <p className="content-text" style={{ lineHeight: '3px' }}>Through my perspective</p>
                                <p className="lower-text">Size:A3           Resolution:300 DPI</p>

                            </div>
                        </div>
                    </div>
                </div>
                <div className='float_left'>
                    <div class="container">
                        <div className="content">
                            <div class="content-overlay"></div>
                            <img className="content-image" style={styles} src={gallery_2} />
                            <div className="content-details fadeIn-bottom" style={styles}>
                                <p className="upper-text">YEAR: 2019</p>
                                <p className="upper-text">Personal Project</p>
                                <h3 className="content-title">Typographic Design Poster</h3>
                                <p className="content-text"style={{ lineHeight: '3px' }}>A poster on famous personality Freddie Mercury ,</p>
                                <p className="content-text"style={{ lineHeight: '3px' }}>expressing his character through type exploration.</p>
                                <p className="content-text"style={{ lineHeight: '3px' }}>Here I tried using letters to create his portrait.</p>
                                <p className="lower-text">Size:A3   Resolution:300 DPI</p>

                            </div>
                        </div>
                    </div>
                </div>
                <div className='float_left'>
                    <div class="container">
                        <div className="content">
                            <div class="content-overlay"></div>
                            <img className="content-image" style={styles} src={gallery_3} />
                            <div className="content-details fadeIn-bottom" style={styles}>
                                <p className="upper-text">YEAR: 2019</p>
                                <p className="upper-text">Personal Project</p>
                                <h3 className="content-title">Typographic Design Poster</h3>
                                <p className="content-text"style={{ lineHeight: '3px' }}>A poster on famous personality Freddie Mercury ,</p>
                                <p className="content-text"style={{ lineHeight: '3px' }}>expressing his character through type exploration.</p>
                                <p className="lower-text">Size:A3   Resolution:300 DPI</p>

                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}