import React, { useEffect, useState, useRef } from "react";
import hoverEffect from "hover-effect";
import overlay from "../../../../assets/displacement_image.jpg"
import img1 from "../../../../assets/Graphic_Design/coke_1.png"
import img2 from "../../../../assets/Graphic_Design/coke_2.jpg"
import img3 from "../../../../assets/test_img.jpg"
import '../css/Coke.css'



export default function Coke() {

    const container = useRef(null);
    useEffect(() => {
        new hoverEffect({
            parent: container.current,
            intensity: .5,
            image1: img1,
            image2: img2,
            displacementImage: overlay,
            imagesRatio: 0.5625,

        })
    }, [container])

    const styles = {
    }

    return (
        <div className='setUp' style={{paddingTop:'10vh'}}>
            <div className='kisan_title' style={{ height: '1vh', textAlign:'center'}}>
                <p className='smallBig_title'>Coca Cola Can Package Redesign</p>
                <p style={{lineHeight:'5px'}}>Coca Cola are know for having creative illustrations on their package on various events, Keeping </p>
                <p style={{lineHeight:'5px'}}>their brands  aesthetic in mind here is an illustration on  TASTE THE FEELING  on a can .</p><br/>
                <p style={{fontWeight:'600'}}>YEAR :  2018</p>
                <p style={{lineHeight:'0px'}}>PERSONAL PROJECT</p>
            </div>
            <div className='center' style={{ paddingTop: '10vh' }}>
                <div
                    className='imgContainer'
                    id="imgContainer"
                    style={{
                        width:'90vw',
                        height:'80vh',
                        paddingTop:'20vh',
                    }}
                    ref={container} />
            </div>
        </div>
    )
}