import React from "react";
import "./css/About.css"

export default function About(){
    return(
        <div className='setUp'>
            <div className = 'all_content_About'>
                <p className='about_me'>About</p>
                <p>Skullcandy Inc. is an American company based in Park City, Utah, that markets headphones,</p>
                <p>earphones, hands-free devices, audio backpacks, MP3 players, and other products.</p><br/>
                <p>This was an Academic project . Our brief was to Rebrand Skullcandy , while</p>
                <p>understanding its place in the market, studying its target group and giving it a whole new</p>
                <p>fresh look with a new logo , Corporate identity , Package design as well as a Website</p>
                <p>while trying to convey the brands youthful story.</p>
                <a className='hover' style ={{position:'relative', fontSize:'1.5vw', top:'8vh'}}>View Process</a>
            </div>

        </div>
    )
}